        width: 1000px; 
        height: 900px;
    }


    #lds-messages-send {
        background: white repeat-x url(../../images/general/table-bg.png); 
        background-position: 0 68px;
        background-size: 100px 1276px; 
        width: 100%; 
        height: 423px;

        .lds-spriteleft,
